.body { 
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 90vh;
    background-size: cover;
    background: #f7faff;
    font-size: 1.5em
}

.button {
    background: white;
    padding: 0.5em;
    padding-left: 1em;
    padding-right: 1em;
    border-color: #004d99;
    color: #004d99;
    margin-right: 1em;
    border-width: thin;
}

.button:hover {
    background-color: #004d99;
    color: white
}

.info-link:hover { 
    background-color: #ebebeb;

  }


  .circleQuestion {
